.posts-feed {
  margin-left: floor(@grid-gutter-width / -2);
  margin-right: ceil(@grid-gutter-width / -2);
  margin-bottom: @line-height-computed;

  border-top: 1px solid @gray-lighter;
  border-bottom: 1px solid @gray-lighter;
}

.posts-feed-item {
  padding: @line-height-computed floor(@grid-gutter-width / 2);

  border-bottom: 1px solid @gray-lighter;
}

.posts-feed-item:last-child {
  border-bottom: 0;
}

// Animate in new or updated threads
@keyframes misago-posts-feed-item-animate {
  0% {
    background-color: @posts-feed-item-animation-color;
  }
  100% {
    background-color: transparent;
  }
}

.posts-feed-item-animate {
  animation-name: misago-posts-feed-item-animate;
  animation-duration: 0.5s;
  animation-iteration-count: 1;
}

// Post
.posts-feed-item-post {
  display: flex;
}

.posts-feed-item-post-bit-side {
  display: none;
}

.posts-feed-item-post-bit-side-avatar {
  display: none;
}

.posts-feed-item-post-bit-side-avatar-sm {
  display: none;
  padding-right: floor(@grid-gutter-width / 2);
}

.posts-feed-item-post-bit-side-avatar-link {
  display: inline-block;
}

.posts-feed-item-post-bit-side-poster {
  display: none;
  padding-top: 3px;
  width: 180px;
}

.posts-feed-item-post-bit-side-poster-link {
  margin-bottom: floor(@line-height-computed / 2);

  &,
  &:link,
  &:visited,
  &:hover,
  &:active,
  &:focus {
    color: var(--color, @text-color);
    font-size: @font-size-large;
    font-weight: bold;
    overflow-wrap: anywhere;
  }
}

.posts-feed-item-post-bit-side-poster-title {
  &,
  &:link,
  &:visited,
  &:hover,
  &:active,
  &:focus {
    color: @text-color;
  }
}

.posts-feed-item-post-main {
  width: 100%;
}

.posts-feed-item-post-bit-top {
  display: flex;
  align-items: center;
}

.posts-feed-item-post-bit-top-avatar {
  margin-right: floor(@grid-gutter-width / 2);
}

.posts-feed-item-post-bit-top-poster-link {
  margin-right: floor(@grid-gutter-width / 2);

  &,
  &:link,
  &:visited,
  &:hover,
  &:active,
  &:focus {
    color: var(--color, @text-color);
    font-weight: bold;
  }
}

.posts-feed-item-post-bit-top-poster-title {
  margin-right: floor(@grid-gutter-width / 2);

  &,
  &:link,
  &:visited,
  &:hover,
  &:active,
  &:focus {
    color: @text-color;
  }
}

.posts-feed-item-post-bit-top-timestamp,
.posts-feed-item-post-bit-top-timestamp-compact,
.posts-feed-item-post-bit-top-post-counter {
  margin-right: floor(@grid-gutter-width / 2);

  &:link,
  &:visited,
  &:hover,
  &:active,
  &:focus {
    color: @gray-light;
  }
}

.posts-feed-item-post-bit-top-timestamp-compact {
  width: 100%;
}

.posts-feed-item-post-bit-top-timestamp {
  display: none;
  width: 100%;
}

.posts-feed-item-post-bit-top-post-new {
  margin-right: 12px;
  padding: 0 4px;

  background-color: @brand-primary;
  border-radius: @border-radius-small;
  color: #fff;
  font-size: @font-size-small;
}

.posts-feed-item-post-edit-form-body {
  margin-top: floor(@line-height-computed / 2);
}

.posts-feed-item-post-body-message {
  padding: floor(@line-height-computed / 2) 0;
}

.posts-feed-item-post-body-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.posts-feed-item-post-body-footer-btn {
  font-weight: 400;
  color: @gray-dark;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &:hover,
  &:focus {
    color: @gray-darker;
    text-decoration: none;
    background-color: @gray-lighter;
  }
  &:active {
    border-color: transparent;
    color: @text-color;
    background-color: darken(@gray-lighter, 10%);
  }
}

@media screen and (min-width: 500px) {
  .posts-feed {
    margin-left: 0;
    margin-right: 0;
  }

  .posts-feed-item {
    padding: @line-height-computed 0;
  }

  .posts-feed-item-post-bit-side,
  .posts-feed-item-post-bit-side-avatar-sm {
    display: block;
  }

  .posts-feed-item-post-bit-top-avatar {
    display: none;
  }
}

@media screen and (min-width: @screen-md-min) {
  .posts-feed-item-post-bit-side {
    display: flex;
    padding-left: 0;
  }

  .posts-feed-item-post-bit-side-avatar-sm,
  .posts-feed-item-post-bit-top-poster-link,
  .posts-feed-item-post-bit-top-poster-title,
  .posts-feed-item-post-bit-top-timestamp-compact {
    display: none;
  }

  .posts-feed-item-post-bit-side-avatar,
  .posts-feed-item-post-bit-side-poster {
    display: block;
  }

  .posts-feed-item-post-bit-side-poster {
    padding-left: ceil(@grid-gutter-width / 2);
    padding-right: @grid-gutter-width;
  }

  .posts-feed-item-post-bit-top-timestamp {
    display: inline;
  }
}
